import React, { useEffect, useState } from 'react';
import { Route, Redirect  } from 'react-router-dom';
import auth, { checkAuth } from '../../helpers/auth';
import { getCookie } from '../../helpers/cookies';
import ClipLoader from 'react-spinners/ClipLoader';

const PrivateRoute = ({ component: Component, ...rest }) => {


  const [loggedUser, setLoggedUser] = useState(null);
  const [gotData, setGotData] = useState(null);
    
  useEffect(() => {
      const fetchAuth = async() => {
          if(getCookie('ulog') !== null) {
              // const { profile } = rest;
              // const res = profile ? await checkAuth('profile') : await checkAuth();
              const res = await checkAuth();
              console.log(res);
              if(res.status === 1) {
                // profile ? setGotData(res.data) : setGotData(null);
                setLoggedUser(true); 
              } else return setLoggedUser(false);
          } else return setLoggedUser(false);
      }

      fetchAuth();
  }, []);

  if(loggedUser === null) return <div className="loading"><ClipLoader size={50} color={'#485877'} /></div>;
  if(!loggedUser) return <Route { ...rest } render={props => (<Redirect to={{ pathname: '/login', state: { from: props.location }}} />)} />

  return <Route { ...rest } render={ props => <Component { ...props } data={gotData} /> } />
}

export default PrivateRoute;